[mol_scroller] {
	overflow: auto;
	overflow-y: scroll;
	flex: 1 1 auto;
	box-sizing: border-box;
	will-change: scroll-position;
	transform: translateZ(0);
	outline: 1px solid var(--mol_skin_light_line);
	max-height: 100%;
	-webkit-overflow-scrolling: touch;
}

@media print {
	[mol_scroller] {
		overflow: visible;
	}
}
